<template>
  <div class="top">
    <h4>
      <van-icon style="color: red" name="award" />
      
    </h4>
    <div class="form">
      <van-form
        @submit="onSubmit"
        @failed="failed"
        onkeydown="if(event.keyCode==13)return false;"
      >
        <van-field
          v-model="form.userName"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
          left-icon="friends-o"
        />
        <van-field
          v-model="form.idCard"
          name=""
          label=""
          placeholder=""
          :rules="[
            { required: true, message: '' },
            { validator: idCardValidator, message: '' },
          ]"
          left-icon="idcard"
        />

        <van-field
          readonly
          clickable
          name="datetimePicker"
          :value="form.dateTime"
          label=""
          placeholder=""
          @click="DateShowPicker = true"
          :rules="[{ required: true, message: '' }]"
          left-icon="tosend"
          right-icon="arrow"
        >
          <!--  -->
          <template #button>
            <div class="longTime" @click.stop="longTime"></div>
          </template>
        </van-field>
        <!--  -->
        <van-popup v-model="DateShowPicker" position="bottom">
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            title=""
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="DateOnConfirm"
            @cancel="DateShowPicker = false"
          />
        </van-popup>

        <van-field
          v-model="form.phone"
          name=""
          label=""
          type="number"
          placeholder=""
          :rules="[
            { required: true, message: '' },
            { validator: phoneValidator, message: '' },
          ]"
          left-icon="phone-o"
        />
        <van-field
          v-model="form.SMSCode"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
          left-icon="shield-o"
        >
          <template #button>
            <van-button
              size="small"
              :disabled="SMSCodeDisable"
              @click.prevent="getSMSCode"
              type="primary"
              >{{ SMSCodeButText }}</van-button
            >
          </template>
        </van-field>

        <!--  -->
        <van-popup :close-on-click-overlay="false" v-model="slideVerifyShow">
          <SlideVerify
            ref="
          slideVerify"
            @changeSlideVerifyShow="changeSlideVerifyShow"
          ></SlideVerify>
        </van-popup>
        <!--  -->
        <van-field
          v-model="form.school"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
          left-icon="wap-home-o"
        />

        <!--  -->
        <van-field
          readonly
          clickable
          name="picker"
          :value="form.gradeChoose"
          label=""
          placeholder=""
          @click="gradeChooseShowPicker = true"
          :rules="[{ required: true, message: '' }]"
          left-icon="user-o"
        >
          <template #button>
            <div><van-icon name="arrow" /></div>
          </template>
        </van-field>

        <!--  -->
        <van-popup v-model="gradeChooseShowPicker" position="bottom">
          <van-picker
            show-toolbar
            :columns="gradeColumns"
            @confirm="gradeChooseOnConfirm"
            @cancel="gradeChooseShowPicker = false"
          />
        </van-popup>

        <!--  -->
        <van-field
          readonly
          clickable
          name="picker"
          :value="form.classChoose"
          :rules="[{ required: true, message: '' }]"
          label=""
          placeholder=""
          @click="classChooseShowPicker = true"
          left-icon="cluster-o"
        >
          <template #button>
            <div><van-icon name="arrow" /></div>
          </template>
        </van-field>

        <!--  -->
        <van-popup v-model="classChooseShowPicker" position="bottom">
          <van-picker
            show-toolbar
            :columns="classColumns"
            @confirm="classChooseOnConfirm"
            @cancel="classChooseShowPicker = false"
          />
        </van-popup>

        <!--  -->
        <van-field
          v-model="form.email"
          name=""
          label=""
          placeholder=""
          :rules="[
            { required: true, message: '' },
            { validator: emailValidator, message: '' },
          ]"
          left-icon="envelop-o"
        >
        </van-field>
        <!--  -  -->
        <van-field
          readonly
          clickable
          name="area"
          :value="form.area"
          label=""
          placeholder=""
          left-icon="location-o"
          @click="showArea = true"
          :rules="[{ required: true, message: '' }]"
        />
        <van-popup v-model="showArea" position="bottom">
          <van-area
            :area-list="areaList"
            @confirm="AreaonConfirm"
            @cancel="showArea = false"
          />
        </van-popup>

        <!--  -->
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            ></van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import SlideVerify from "@/components/SlideVerify .vue";
import { Toast } from "vant";
import Vue from "vue";

import isIdcard from "@/views/whiteList/JS/index";
import areaList from "./areaList";
export default {
  name: "",
  components: { SlideVerify },

  data() {
    return {
      // 
      form: {
        userName: "",
        idCard: "",
        dateTime: "",
        phone: "",
        SMSCode: "",
        school: "",
        gradeChoose: "",
        classChoose: "",
        email: "",
        area: "",
      },

      //   
      DateShowPicker: false,
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2100, 1, 1),
      currentDate: "",

      //   
      slideVerifyShow: false,
      slideVerifyText: "",
      SMSCodeDisable: false,
      SMSCodeButText: "",

      //   
      gradeColumns: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      gradeChooseShowPicker: false,
      //   
      classColumns: ["", "", "", "", "", ""],
      classChooseShowPicker: false,
      // 
      showArea: false,
      areaList: areaList, //  Area 
    };
  },
  created() {
    //   
    let data = new Date();
    let year = data.getFullYear();
    let month = data.getMonth();
    let day = data.getDate();
    console.log(year, month, day);
    // 
    this.currentDate = new Date(year, month, day);
  },
  mounted() {},
  methods: {
    //    
    onSubmit(values) {
      console.log("", values);
      this.$router.push({
        path: "/insuranceApplication",
      });
    },
    //    
    failed(errorInfo) {
      console.log("failed", errorInfo);
    },
    // 
    idCardValidator() {
      return isIdcard(this.form.idCard);
    },
    // 
    phoneValidator() {
      return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.form.phone);
    },
    //  email
    emailValidator() {
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      return reg.test(this.form.email);
    },
    // 
    DateOnConfirm(time) {
      console.log(time.getFullYear());
      this.form.dateTime = `${time.getFullYear()}${
        time.getMonth() + 1
      }${time.getDate()}`;
      this.DateShowPicker = false;
    },
    // 
    longTime() {
      this.form.dateTime = "";
    },

    SMSCodeCountdown() {
      let time = 10;
      let countdown = setInterval(() => {
        time--;
        this.SMSCodeButText = time + "";
        if (time == 0) {
          this.SMSCodeButText = "";
          this.SMSCodeDisable = false;
          clearInterval(countdown);
        }
      }, 1000);
    },
    // 
    getSMSCode() {
      this.slideVerifyShow = true;
      this.$nextTick(() => {});
    },

    // 
    onSuccess() {
      this.slideVerifyShow = false;
      Toast.success("");
      this.SMSCodeDisable = true;
      this.SMSCodeCountdown();
    },
    changeSlideVerifyShow() {
      this.onSuccess();
    },
    // 
    gradeChooseOnConfirm(value) {
      this.form.gradeChoose = value;
      this.gradeChooseShowPicker = false;
    },
    // 
    classChooseOnConfirm(value) {
      this.form.classChoose = value;
      this.classChooseShowPicker = false;
    },
    // 
    AreaonConfirm(values) {
      console.log("", values);
      this.form.area = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join("/");
      this.showArea = false;
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.van-icon {
  color: #0000ff;
}
.top {
  padding: 20px;
}
.form {
  margin-top: 20px;
}

:deep(.van-field__left-icon) {
  margin-right: 14px;
  color: #0066ff;
  width: 20px;
  height: 20px;
}
:deep(.van-field__left-icon .van-icon),
.van-field__right-icon .van-icon {
  font-size: 25px;
}

.longTime {
  color: blue;
  border-radius: 5px;
  border: #0000ff 1px solid;
  padding: 6px;
}

:deep(.van-cell) {
  padding: 17px 16px;
}
</style>
