export default function isIdcard(value) {
    var sId = value;
    if (sId.length == 15) {
        if (!/^\d{14}(\d|x)$/i.test(sId)) {
            this.message = "";
            return false;
        } else {
            sId = sId.substr(0, 6) + '19' + sId.substr(6, 9);
            sId += getVCode(sId);
        }
    }

    function getVCode(CardNo17) {
        var Wi = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1);
        var Ai = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2');
        var cardNoSum = 0;
        for (var i = 0; i < CardNo17.length; i++) cardNoSum += CardNo17.charAt(i) * Wi[i];
        var seq = cardNoSum % 11;
        return Ai[seq];
    }
    var aCity = { 11: "", 12: "", 13: "", 14: "", 15: "", 21: "", 22: "", 23: "", 31: "", 32: "", 33: "", 34: "", 35: "", 36: "", 37: "", 41: "", 42: "", 43: "", 44: "", 45: "", 46: "", 50: "", 51: "", 52: "", 53: "", 54: "", 61: "", 62: "", 63: "", 64: "", 65: "", 71: "", 81: "", 82: "", 91: "" };

    var iSum = 0;
    var info = "";
    if (!/^\d{17}(\d|x)$/i.test(sId)) {
        return false;
    }
    sId = sId.replace(/x$/i, "a");
    if (aCity[parseInt(sId.substr(0, 2))] == null) {
        return false;
    }
    var sBirthday = sId.substr(6, 4) + "-" + Number(sId.substr(10, 2)) + "-" + Number(sId.substr(12, 2));
    var d = new Date(sBirthday.replace(/-/g, "/"));
    if (sBirthday != (d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate())) {
        return false;
    }
    for (var i = 17; i >= 0; i--) iSum += (Math.pow(2, i) % 11) * parseInt(sId.charAt(17 - i), 11);
    if (iSum % 11 != 1) {
        return false;
    }
    return true;
}