<template>
  <div class="top">
    <div class="insured-people">
      <h4>
        <van-icon style="color: red" name="award" />
        
      </h4>
      <div class="form">
        <van-form
          @submit="onSubmit"
          onkeydown="if(event.keyCode==13)return false;"
        >
          <van-field
            v-model="form.userName"
            name=""
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
            left-icon="friends-o"
          />
          <van-field
            v-model="form.idCard"
            name=""
            label=""
            placeholder=""
            :rules="[
              { required: true, message: '' },
              { validator: idCardValidator, message: '' },
            ]"
            left-icon="idcard"
          >
            <!--  -->
            <template #button>
              <van-icon name="scan" @click="scanIdCard" /> </template
          ></van-field>

          <van-field
            readonly
            clickable
            name="datetimePicker"
            :value="form.dateTime"
            label=""
            placeholder=""
            @click="DateShowPicker = true"
            :rules="[{ required: true, message: '' }]"
            left-icon="tosend"
            right-icon="arrow"
          >
            <!--  -->
            <template #button>
              <div class="longTime" @click.stop="longTime"></div>
            </template>
          </van-field>
          <!--  -->
          <van-popup v-model="DateShowPicker" position="bottom">
            <van-datetime-picker
              v-model="currentDate"
              type="date"
              title=""
              :min-date="minDate"
              :max-date="maxDate"
              @confirm="DateOnConfirm"
              @cancel="DateShowPicker = false"
            />
          </van-popup>
          <!--  -->
          <van-field
            v-model="form.phone"
            name=""
            label=""
            type="number"
            placeholder=""
            :rules="[
              { required: true, message: '' },
              { validator: phoneValidator, message: '' },
            ]"
            left-icon="phone-o"
          />
          <van-field
            v-model="form.SMSCode"
            name=""
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
            left-icon="shield-o"
          >
            <template #button>
              <!-- <button @click.prevent="getSMSCode" type="button">asfas</button> -->
              <van-button
                @click.prevent="getSMSCode"
                type="primary"
                size="small"
                :disabled="SMSCodeDisable"
                >{{ SMSCodeButText }}</van-button
              >
            </template>
          </van-field>

          <!--  -->
          <van-popup :close-on-click-overlay="false" v-model="slideVerifyShow">
            <div @touchmove.prevent>
              <slide-verify
                :l="42"
                :r="10"
                :imgs="slideImgs"
                :w="310"
                :h="155"
                @success="onSuccess"
                @fail="onFail"
                @refresh="onRefresh"
                :slider-text="slideVerifyText"
              ></slide-verify>
            </div>
          </van-popup>

          <!--  -->
          <van-field
            v-model="form.email"
            name=""
            label=""
            placeholder=""
            :rules="[
              { required: true, message: '' },
              { validator: emailValidator, message: '' },
            ]"
            left-icon="envelop-o"
          >
          </van-field>

          <!--        -->
          <div class="be-people">
            <h4>
              <van-icon style="color: red" name="award" />
              
            </h4>
            <van-field
              v-model="form.userName"
              name=""
              label=""
              placeholder=""
              :rules="[{ required: true, message: '' }]"
              left-icon="friends-o"
            />
            <van-field
              v-model="form.idCard"
              name=""
              label=""
              placeholder=""
              :rules="[
                { required: true, message: '' },
                { validator: idCardValidator, message: '' },
              ]"
              left-icon="idcard"
            >
              <!--  -->
              <template #button>
                <van-icon name="scan" @click="scanIdCard" /> </template
            ></van-field>

            <van-field
              readonly
              clickable
              name="datetimePicker"
              :value="form.dateTime"
              label=""
              placeholder=""
              @click="DateShowPicker = true"
              :rules="[{ required: true, message: '' }]"
              left-icon="tosend"
              right-icon="arrow"
            >
              <!--  -->
              <template #button>
                <div class="longTime" @click.stop="longTime"></div>
              </template>
            </van-field>
            <!--  -->
            <van-popup v-model="DateShowPicker" position="bottom">
              <van-datetime-picker
                v-model="currentDate"
                type="date"
                title=""
                :min-date="minDate"
                :max-date="maxDate"
                @confirm="DateOnConfirm"
                @cancel="DateShowPicker = false"
              />
            </van-popup>
          </div>

          <!--  -->
          <van-field
            v-model="form.school"
            name=""
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
            left-icon="wap-home-o"
          />

          <!--  -->
          <van-field
            readonly
            clickable
            name="picker"
            :value="form.gradeChoose"
            label=""
            placeholder=""
            @click="gradeChooseShowPicker = true"
            :rules="[{ required: true, message: '' }]"
            left-icon="user-o"
          >
            <template #button>
              <div><van-icon name="arrow" /></div>
            </template>
          </van-field>

          <!--  -->
          <van-popup v-model="gradeChooseShowPicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="gradeColumns"
              @confirm="gradeChooseOnConfirm"
              @cancel="gradeChooseShowPicker = false"
            />
          </van-popup>

          <!--  -->
          <van-field
            readonly
            clickable
            name="picker"
            :value="form.classChoose"
            :rules="[{ required: true, message: '' }]"
            label=""
            placeholder=""
            @click="classChooseShowPicker = true"
            left-icon="cluster-o"
          >
            <template #button>
              <div><van-icon name="arrow" /></div>
            </template>
          </van-field>

          <!--  -->
          <van-popup v-model="classChooseShowPicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="classColumns"
              @confirm="classChooseOnConfirm"
              @cancel="classChooseShowPicker = false"
            />
          </van-popup>

          <!--  -  -->
          <van-field
            readonly
            clickable
            name="area"
            :value="form.area"
            label=""
            placeholder=""
            left-icon="location-o"
            @click="showArea = true"
            :rules="[{ required: true, message: '' }]"
          />
          <van-popup v-model="showArea" position="bottom">
            <van-area
              :area-list="areaList"
              @confirm="AreaonConfirm"
              @cancel="showArea = false"
            />
          </van-popup>

          <!--  -->
          <div style="margin: 16px">
            <van-button
              round
              block
              type="info"
              native-type="submit"
              @click="gotoInsuranceApplication"
              ></van-button
            >
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
import Vue from "vue";
import SlideVerify from "vue-monoplasty-slide-verify";

Vue.use(SlideVerify);
import isIdcard from "@/views/whiteList/JS/index";
import areaList from "./areaList";
export default {
  name: "",
  components: {},

  data() {
    return {
      slideImgs: [
        "https://img1.baidu.com/it/u=8957568,3772515509&fm=253&fmt=auto&app=138&f=JPEG?w=658&h=411",
        "https://img2.baidu.com/it/u=2703411705,3588113199&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=281",
        "https://img2.baidu.com/it/u=3116610008,4122310051&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500",
        "https://www4.bing.com//th?id=OHR.ArambolBeach_ZH-CN2149857876_1920x1080.jpg&rf=LaDigue_1920x1080.jpg&pid=hp",
        "https://img1.baidu.com/it/u=740578140,3088309952&fm=253&fmt=auto&app=120&f=JPEG?w=960&h=540",
        "https://img0.baidu.com/it/u=1995209268,3930309933&fm=253&fmt=auto&app=120&f=JPEG?w=667&h=500",
      ], // 
      // 
      form: {
        userName: "",
        idCard: "",
        dateTime: "",
        phone: "",
        SMSCode: "",
        school: "",
        gradeChoose: "",
        classChoose: "",
        email: "",
        area: "",
      },

      //   
      DateShowPicker: false,
      DateShowPickerA: false,
      
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2100, 1, 1),
      currentDate: "",

      //   
      slideVerifyShow: false,
      slideVerifyText: "",
      SMSCodeDisable: false,
      SMSCodeButText: "",

      //   
      gradeColumns: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      gradeChooseShowPicker: false,
      //   
      classColumns: ["", "", "", "", "", ""],
      classChooseShowPicker: false,
      // 
      showArea: false,
      areaList: areaList, //  Area 
    };
  },
  created() {
    //   
    let data = new Date();
    let year = data.getFullYear();
    let month = data.getMonth();
    let day = data.getDate();
    console.log(year, month, day);
    // 
    this.currentDate = new Date(year, month, day);
  },
  mounted() {},
  methods: {
    //    
    onSubmit(values) {
      console.log("submit", values);
    },
    // 
    idCardValidator() {
      return isIdcard(this.form.idCard);
    },
    // 
    phoneValidator() {
      return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.form.phone);
    },
    //  email
    emailValidator() {
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      return reg.test(this.form.email);
    },
    // 
    DateOnConfirm(time) {
      console.log(time.getFullYear());
      this.form.dateTime = `${time.getFullYear()}${
        time.getMonth() + 1
      }${time.getDate()}`;
      this.DateShowPicker = false;
    },
    // 
    longTime() {
      this.form.dateTime = "";
    },

    SMSCodeCountdown() {
      let time = 10;
      let countdown = setInterval(() => {
        time--;
        this.SMSCodeButText = time + "";
        if (time == 0) {
          this.SMSCodeButText = "";
          this.SMSCodeDisable = false;
          clearInterval(countdown);
        }
      }, 1000);
    },
    // 
    getSMSCode() {
      console.log("");

      this.slideVerifyShow = true;
      this.onRefresh();
    },

    // 
    onSuccess() {
      this.msg = "login success";
      this.slideVerifyShow = false;
      Toast.success("");
      this.SMSCodeDisable = true;
      this.SMSCodeCountdown();
    },
    // 
    onFail() {
      console.log("");
    },
    // 
    onRefresh() {
      console.log("");
    },
    // 
    gradeChooseOnConfirm(value) {
      this.form.gradeChoose = value;
      this.gradeChooseShowPicker = false;
    },
    // 
    classChooseOnConfirm(value) {
      this.form.classChoose = value;
      this.classChooseShowPicker = false;
    },
    // 
    AreaonConfirm(values) {
      console.log("", values);
      this.form.area = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join("/");
      this.showArea = false;
    }, // 
    scanIdCard() {
      this.$router.push({
        path: "/scanIdCard",
      });
    },
    gotoInsuranceApplication() {
      this.$router.push({
        path: "/insuranceApplication",
      });
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.van-icon {
  color: #0000ff;
}
.top {
  padding: 20px;
}
.form {
  margin-top: 20px;
}

:deep(.van-field__left-icon) {
  margin-right: 14px;
  color: #0066ff;
  width: 20px;
  height: 20px;
}
:deep(.van-field__left-icon .van-icon),
.van-field__right-icon .van-icon {
  font-size: 25px;
}

.longTime {
  color: blue;
  border-radius: 5px;
  border: #0000ff 1px solid;
  padding: 6px;
}

:deep(.van-cell) {
  padding: 17px 16px;
}

.be-people {
  margin-top: 10px;
}
</style>
